@import url(https://fonts.googleapis.com/css2?family=Alata&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  font-family: "Alata", sans-serif;
  text-align: center;
}

p {
  font-size: 15px;
  margin: 5px;
}

a {
  color: white;
}

.backImg {
  width: 100vw;
  height: 100vh;
  background-size: cover;
  top: 0;
  flex-direction: column;
  color: #fff;
}

.titleBanner {
  background: linear-gradient(rgba(163, 105, 95, 0.7), rgba(75, 85, 86, 0.7));
  width: 100%;
  min-width: 690px;
  height: 40vh;
  min-height: 315px;
  margin: 0;
  position: relative;
  top: 0;
  justify-content: center;
  align-items: center;
  display: flex;
  flex-direction: column;
}

.title {
  display: flex;
  color: white;
  text-align: center;
  font-size: 2.5em;
}

.imgSearch {
  display: flex;
  flex-direction: column;
  margin: auto;
  text-align: center;
  vertical-align: middle;
  height: 3em;
  width: auto;
  justify-content: center;
  align-items: center;
  position: relative;
  -webkit-animation: shake 1s 2 linear;
          animation: shake 1s 2 linear;
}

@-webkit-keyframes shake {
  0% {
    transform: translate(5px, 0);
  }
  50% {
    transform: translate(-5px, 0);
  }
  100% {
    transform: translate(0, 0);
  }
}

@keyframes shake {
  0% {
    transform: translate(5px, 0);
  }
  50% {
    transform: translate(-5px, 0);
  }
  100% {
    transform: translate(0, 0);
  }
}

.imgSearch:hover {
  transform: scale(1.1);
  transition: 0.2s all ease-in;
}

.searchContainer {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.searchField:focus {
  width: 200px;
  height: 40px;
  border-radius: 40px;
  padding: 0 20px;
  line-height: 2em;
  font-weight: 100;
  font-size: 12px;
  cursor: auto;
  font-size: medium;
  font-family: "Alata", sans-serif;
}

.searchField {
  float: right;
  border: 3px solid #fff;
  outline: none;
  width: 40px;
  cursor: pointer;
  transition: 0.2s all ease-in;
  height: 40px;
  color: #fff;
  border-radius: 40px;
  background: none;
  font-size: 0;
}

.close span {
  width: 3px;
  height: 20px;
  border-radius: 2px;
  background: #fff;
  display: inline-block;
}

.x1 {
  position: absolute;
  right: 24px;
  top: 13px;
  transform: rotate(-45deg);
  opacity: 0;
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
  transition: 0.2s all ease-in;
}

.x2 {
  position: absolute;
  right: -3px;
  top: 33px;
  transform: rotate(-45deg);
  transition: 0.2s all ease-in;
}

.searchField:focus + div .x1 {
  opacity: 1;
  transition-delay: 0.2s;
  transform: rotate(45deg);
}

.searchField:focus + div .x2 {
  right: 24px;
  top: 13px;
}

@media only screen and (max-width: 1000) {
  * {
    height: auto;
  }
  .titleBanner {
    height: 100vh;
  }
}

.imgSearchResults {
  display: none;
  flex-direction: column;
  flex-wrap: wrap;
  position: absolute;
  height: 200vh;
  width: 100%;
  min-width: 1000px;
  margin: 0;
  color: #181818;
  overflow: hidden;
  margin-top: 20vh;
  vertical-align: bottom;
  background-color: white;
}

.imgSearchResult img {
  object-fit: cover;
  width: 33.3vw;
  min-width: 333px;
  overflow: auto;
}

.colorCode {
  text-align: center;
  font-size: 1em;
  margin-top: -5px;
  padding-top: 8px;
  padding-bottom: 8px;
}

.imgSearchResult img:hover {
  opacity: 0;
  transition: 0.5s ease;
}

.colorCard {
  object-fit: cover;
  width: 33.3vw;
  min-width: 333px;
}

@media only screen and (max-width: 1000px) {
  .imgSearchResults {
    height: auto;
  }

  .colorCard {
    min-width: 100vw;
    max-width: 230px;
  }

  .imgSearchResult img {
    object-fit: ;
    min-width: 100vw;
    max-width: 230px;
  }
}

.filter {
  padding: 3px;
  display: inline-block;
  border-radius: 4px;
  margin: auto;
  margin-top: 15px;
}

.allOptions {
  min-height: 30px;
  display: inline;
}

.allOptions .colorOption {
  display: inline-block;
}

.allOptions input[type="radio"] {
  display: none;
  height: 0;
  width: 0;
}

.colorOption:hover {
  transform: scale(1.5);
}

.colorOption {
  height: 20px;
  width: 20px;
  border-radius: 50%;
  transition: all 0.15s ease;
  margin: 10px;
  border: 2px solid white;
}

#yellow {
  background: #ffe240;
}

#orange {
  background: #ffb13c;
}

#red {
  background: #e64343;
}

#green {
  background: #3dda90;
}
#blue {
  background: #5564ff;
}

#purple {
  background: #ec71fc;
}

#gray {
  background: #9e9e9e;
}

